import React, { createContext ,useState} from 'react';
import './App.css'
import { BrowserRouter as Router, Switch, Route, Link } from "react-router-dom"
import Main from './Main'
import Home from './Home';
import Logout from './Logout'




function App() {

  return (
    <Router>
      <div className="App">
      <Switch>
          <Route path="/signin">
          </Route>
          <Route path="/signup">
          </Route>
          <Route path='/home'>
            <Home/>
          </Route>
          <Route path='/logout'>
            <Logout/>
          </Route>
          <Route path="/">
            <Main />
          </Route>
        </Switch>
      </div>
    </Router>
  )
}

export default App
