import React, { useState, useEffect } from 'react'
import './Main.css'
import './Message.css'
import { auth, firestore } from './config.js'
import Sendmessage from './Sendmessage'

const Message = ({ messageto }) => {
    const [title, setTitle] = useState(null)
    const [mail, setMail] = useState(null)
    const [url, setUrl] = useState(null)
    const [message, setMessage] = useState('')
    const [messages, setMessages] = useState(null)

    const wrtie = (e) => {
        setMessage(e.target.value)

    }

    const submit = (e) => {
        e.preventDefault()
        const timestamp = Date.now()
        firestore.collection(`messages`).add(
            {
                sender: mail,
                receiver: title,
                message,
                timestamp
            }
        )
        setMessage('')
    }

    useEffect(() => {
        const user = auth.currentUser
        setTitle(user.email)
        firestore.collection(`messages`).orderBy('timestamp').onSnapshot(
            snap => {
                setMessages(snap.docs.map(doc => (doc.data())))
            }
        )
    }, [message])

    useEffect(() => {
        const user = auth.currentUser
        if (user) {
            setMail(user.email)
        }
        firestore.collection('users').onSnapshot(
            snp => {
                const images = snp.docs.map(doc => (doc.data()))
                const image = images.filter(img => (img.email === messageto))
                console.log(image)
                if (image.length === 1) {
                    const urlBg = image[0].url
                    setUrl(urlBg)
                }
            }
        )
    }, [messageto])


    return (
        <div className='message'>

            <div className='imgholder'>
                <img className='img' src={url} alt={messageto} />
            </div>
            <div className='nameuser'>
                {title}
            </div>
            <div className='inputholder'>
                <form onSubmit={submit}>
                    <input className='input' value={message} onChange={wrtie} placeholder='wrtie youe message here...' />
                </form>
            </div>
            <div className='canvas'>
    {messages && messages.map(msg=>((msg.sender===messageto)||(msg.receiver===mail)?<Sendmessage key={msg.timestamp} user={mail} to={title}message={msg}/>:null))}
            </div>
        </div>
    )
}

export default Message
